#footer{
    background-color: #111;
    height: 40vh;
    padding-top: 2rem;    
}

.footer-contacts{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 20vh;
    margin-bottom: 3.1rem;
}

.footer-links{
    text-decoration: none;
    color: #FFF;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.contact-info{
    margin-left: 1rem;
}

.footer-logo{
    display: flex;
    align-items: center;
    justify-content: center;
    width:50%;
}

.footer-title{
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 2rem;
}

.footer-icons{
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    width: 50%;
    text-decoration: none;
}


.footer-end{
    text-align: center;
    color: #FFF;
}

@media screen and (max-width: 800px){
    .footer-logo{
        font-size: 0.5rem;
        width: 30%;
        margin-left: 1rem;
    }
    
    .footer-icons{
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: center;
        width: 70%;
        margin-left: 3rem;
        text-decoration: none;
    }
    .footer-links{
        margin: 1rem;
    }
}