.about-container{
    background-color: #111;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    flex-direction: column;
    align-items:center;
}

span{
    color:#FFF;
}

.title{
    color:#ffcc00;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    align-self: center;
}

.icon{
    position: absolute;
    left:21%;
    background-color: #111;
    color: #7f7fff;
}

.all-text{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-left: 0.1px solid #FFF;
    background-color: #111;
    width:70%;
    margin-bottom: 6%;
}

.all-text-title{
    margin-top: 5rem;
}

.description{
    text-align: justify;

}

.text{
    color: #EEE;
    padding-left: 15%;
}


@media screen and (max-width: 800px){
    
    .about-container{
        padding-left: 0;
        padding-right: 0;
    }
}
