@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about-container{
    background-color: #111;
    padding-left: 10%;
    padding-right: 10%;
    display: flex;
    flex-direction: column;
    align-items:center;
}

span{
    color:#FFF;
}

.title{
    color:#ffcc00;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
    align-self: center;
}

.icon{
    position: absolute;
    left:21%;
    background-color: #111;
    color: #7f7fff;
}

.all-text{
    display: flex;
    flex-direction: column;
    align-items: center;
    border-left: 0.1px solid #FFF;
    background-color: #111;
    width:70%;
    margin-bottom: 6%;
}

.all-text-title{
    margin-top: 5rem;
}

.description{
    text-align: justify;

}

.text{
    color: #EEE;
    padding-left: 15%;
}


@media screen and (max-width: 800px){
    
    .about-container{
        padding-left: 0;
        padding-right: 0;
    }
}

.all_container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
#footer{
    background-color: #111;
    height: 40vh;
    padding-top: 2rem;    
}

.footer-contacts{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 20vh;
    margin-bottom: 3.1rem;
}

.footer-links{
    text-decoration: none;
    color: #FFF;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.contact-info{
    margin-left: 1rem;
}

.footer-logo{
    display: flex;
    align-items: center;
    justify-content: center;
    width:50%;
}

.footer-title{
    text-align: center;
    margin: 0;
    padding: 0;
    margin-bottom: 2rem;
}

.footer-icons{
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    width: 50%;
    text-decoration: none;
}


.footer-end{
    text-align: center;
    color: #FFF;
}

@media screen and (max-width: 800px){
    .footer-logo{
        font-size: 0.5rem;
        width: 30%;
        margin-left: 1rem;
    }
    
    .footer-icons{
        display: flex;
        flex-direction: column;
        align-self: center;
        justify-content: center;
        width: 70%;
        margin-left: 3rem;
        text-decoration: none;
    }
    .footer-links{
        margin: 1rem;
    }
}
#home{
    background-color: #111;

}
.home-container{
    color: #FFF;
    margin: auto auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 30%;
    z-index: 1;
}

span {
    color: #ffcc00;
}

.intro {
    font-size: 3rem;
    margin:0;
    font-family: 'Roboto', sans-serif;
}



.secondary{
    font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 800px){
    #home{
        margin-top: 20%;
        height: 30vh;
    }
    .home-container{
        top: 20%;
        left: 20%;
        text-align: center;
    }
    .intro, .secondary {
        font-size: 1rem;
        margin:0.2rem;
        font-family: 'Roboto', sans-serif;
    }
}
.navbar-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #212121;
    color: #FFF;
    height: 10vh;
    width: 100%;
    position: fixed;
    top: 0%;
    left: 0%;
    z-index: 100;
}

.buttons-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 1.5rem;
    width: 40%;
}

.navbar-link {
    padding: 0.5rem;
    height: 40%;
    cursor: pointer;
    text-decoration: none;
    color: #FFF;
    transition: transform 0.5s ease-in;
}
.navbar-link:hover{
    border-bottom: 1px solid #7f7fff;

}

.logo{
    margin: 1rem;
    border-bottom: 2px solid #7f7fff;
    color: #ffcc00;
    width: 16rem;
}

@media screen and (max-width: 800px){
    .navbar-container{
        height: 11%;
        width: 100vw;
    }
    .navbar-container, .buttons-container{
        flex-direction: column;
        align-items: center;
    }
    .buttons-container{
        margin: 0;
        margin-bottom: 0.5rem;
    }
    .logo{
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
        padding-bottom: 0;
    }
    .menu-link{
        padding: 0;
        color:#000;
        text-decoration: none
    }
}
.card{
    width: 25vw;
    height: 70vh;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background: linear-gradient(to bottom, #ffcc00 30%, white 0%); 
    background-color: #ffcc00;
    margin: 3rem;
}




.card-img{
    margin-top: 10%;
    width: 60%;
    height: 35%;
    border: 1px solid #000;
}

.card-title{
    font-size: 1.3rem;
    color: #7f7fff;
}

.card-text{
    width: 80%;
    height: 60%;
    text-align: justify;
    text-justify: inter-word;
}

.texts {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 80%;
    height: 70%;
    font-family: 'Noto Sans KR', sans-serif;
}

.card-footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-grow: 1;
    width: 100%;
}

.card-link{
    text-decoration: none;
}

@media screen and (max-width: 800px){
    .card{    
        width: 80vw;
        height: 60vh;
    }

}

.scroll-box {
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  
  .scroll-box__wrapper {
    width: 100%;
    height: 100%;
    overflow-y: hidden;
    overflow-x: scroll;
     -ms-overflow-style: none; /* IE */
     overflow: -moz-scrollbars-none; /* Firefox */
  }
  
   .scroll-box__wrapper::-webkit-scrollbar {
     display: none; /* Chrome and Safari */
    }
  
  .scroll-box__container {
    height: 100%;
    display: inline-flex;
  }
#projects{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #111 ;
}



