.navbar-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: #212121;
    color: #FFF;
    height: 10vh;
    width: 100%;
    position: fixed;
    top: 0%;
    left: 0%;
    z-index: 100;
}

.buttons-container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 1.5rem;
    width: 40%;
}

.navbar-link {
    padding: 0.5rem;
    height: 40%;
    cursor: pointer;
    text-decoration: none;
    color: #FFF;
    transition: transform 0.5s ease-in;
}
.navbar-link:hover{
    border-bottom: 1px solid #7f7fff;

}

.logo{
    margin: 1rem;
    border-bottom: 2px solid #7f7fff;
    color: #ffcc00;
    width: 16rem;
}

@media screen and (max-width: 800px){
    .navbar-container{
        height: 11%;
        width: 100vw;
    }
    .navbar-container, .buttons-container{
        flex-direction: column;
        align-items: center;
    }
    .buttons-container{
        margin: 0;
        margin-bottom: 0.5rem;
    }
    .logo{
        margin-top: 0.5rem;
        margin-bottom: 0.5rem;
        padding-bottom: 0;
    }
    .menu-link{
        padding: 0;
        color:#000;
        text-decoration: none
    }
}