#home{
    background-color: #111;

}
.home-container{
    color: #FFF;
    margin: auto auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 50%;
    left: 30%;
    z-index: 1;
}

span {
    color: #ffcc00;
}

.intro {
    font-size: 3rem;
    margin:0;
    font-family: 'Roboto', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');



.secondary{
    font-family: 'Roboto', sans-serif;
}

@media screen and (max-width: 800px){
    #home{
        margin-top: 20%;
        height: 30vh;
    }
    .home-container{
        top: 20%;
        left: 20%;
        text-align: center;
    }
    .intro, .secondary {
        font-size: 1rem;
        margin:0.2rem;
        font-family: 'Roboto', sans-serif;
    }
}