.card{
    width: 25vw;
    height: 70vh;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background: linear-gradient(to bottom, #ffcc00 30%, white 0%); 
    background-color: #ffcc00;
    margin: 3rem;
}




.card-img{
    margin-top: 10%;
    width: 60%;
    height: 35%;
    border: 1px solid #000;
}

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100&display=swap');

.card-title{
    font-size: 1.3rem;
    color: #7f7fff;
}

.card-text{
    width: 80%;
    height: 60%;
    text-align: justify;
    text-justify: inter-word;
}

.texts {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 80%;
    height: 70%;
    font-family: 'Noto Sans KR', sans-serif;
}

.card-footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-grow: 1;
    width: 100%;
}

.card-link{
    text-decoration: none;
}

@media screen and (max-width: 800px){
    .card{    
        width: 80vw;
        height: 60vh;
    }

}
